import React from 'react';
import DefaultCard from '@/componentNew/base/Card/DefaultCard';
import styles from './app.less';

// 工具
const App = ({
  data,
  onClickItem
}: any) => {
  return (
    <DefaultCard className={styles.tools}>
      <div className={styles.header}>
        <span>工具</span>
      </div>
      <div className={styles.content}>
        <div className={styles.left}>
          <div className={styles.item}>
            {data.main && (
              <div className={styles.wrap} onClick={() => onClickItem(data.main)}>{data.main.name}</div>
            )}
          </div>
        </div>
        <div className={styles.right}>
          {data.sub.map((item: any, index: any) => (
            <div key={index} className={styles.item} onClick={() => onClickItem(item)}>
              <div className={styles.wrap}>{item.name}</div>
            </div>
          ))}
        </div>
      </div>
    </DefaultCard>
  );
};

export default App;
