import React from 'react';
import classnames from 'classnames';
import DefaultCard from '@/componentNew/base/Card/DefaultCard';
import { formatDate } from './tool';
import styles from './app.less';

const weekCNames = ['日', '一', '二', '三', '四', '五', '六'];

const App = () => {
  const {
    year, month, day,
    week,
    // firstDayWeek, curDays,
  } = formatDate();
  /**
   * 渲染小组件面板
   */
  const renderCalendarPanel = () => {
    // const days = (new Array(firstDayWeek).fill(''))
    //   .concat(new Array(curDays).fill('').map((i, index) => index + 1))
    //   .concat(new Array(35 - firstDayWeek - curDays).fill(''));

    const rows = 7;
    const cols = 6;
    const currentWeekDay = new Date(year, month - 1, 1).getDay();
    const days = Array(rows * cols).fill('').map((n, index) => {
      const item = new Date(year, month - 1, 0 - currentWeekDay + 1 + index);
      return {
        month: item.getMonth() + 1, // 月
        day: item.getDate(), // 日
        isToday: item.getFullYear() === year && item.getMonth() + 1 === month && item.getDate() === day,
        isOtherMonth: item.getMonth() + 1 !== month,
        isWeekEnd: [0, 6].includes(item.getDay()),
      };
    });

    return (
      <div className={styles.calendar}>
        <div className={styles.calendarWeek}>
          {weekCNames.map((i, index) => (
            <div key={i}><span className={classnames([0, 6].includes(index) && styles.weekend)}>{i}</span></div>
          ))}
        </div>
        <div className={styles.calendarDays}>
          {days.map((i, index) => (
            <div key={`i-${index}`}>
              <span className={classnames({
                [styles.weekend]: i.isWeekEnd,
                [styles.other]: i.isOtherMonth,
                [styles.isToday]: i.isToday,
              })}>{i.day}</span>
            </div>
          ))}
        </div>
      </div>
    );
  };

  return (
    <DefaultCard className={styles.warp}>
      <div className={classnames(styles.container)}>
        <div className={classnames(styles.mainPanel, styles.column, styles.needSubPanel, styles['jc-between'])} style={{ width: '36%', padding: 8 }}>
          <div className={classnames(styles.iconWrap)}>
            {/* <CalendarOutlined /> */}
            {/* <div className={classnames(styles.f36)} style={{ paddingRight: '4px' }}>{day}</div> */}
          </div>
          <div className={classnames(styles.flex, styles['ai-baseline'], styles['w-full'], styles['jc-start'])}>
            <div className={classnames(styles.f36)} style={{ paddingRight: '4px' }}>{day}</div>
            <div className={classnames(styles.f12)}>{`${month} / ${year}`}</div>
          </div>
        </div>
        <div className={classnames(styles.subPanel)}>
          {renderCalendarPanel()}
        </div>
      </div>
    </DefaultCard>
  );
};

export default App;
