import React, { useState } from 'react';
import { Spin } from 'antd';
import Orbit from '@/componentNew/base/Orbit';
import styles from './index.less';

type IProps = {
  item: {
    name?: string;
    url: string;
    noWithUrl?: boolean;
  };
};

const Panel = ({
  item
}: IProps) => {
  const [initLoading, setInitLoading] = useState(true);

  return (
    <div className={styles.container}>
      {!item.noWithUrl && (
        <div className={styles.browserHeader}>
          <div className={styles.withUrl}>{item.name || item.url}</div>
        </div>
      )}
      <div className={styles.browserMockup}>
        <Spin spinning={initLoading} indicator={<Orbit text='加载中' />}>
          <iframe style={{ opacity: initLoading ? 0 : 1 }} className={styles.browserMockup} src={item.url} onLoad={() => setInitLoading(false)} />
        </Spin>
      </div>
    </div>
  );
};

export default Panel;
