import React from 'react';
import App from './app';
import Popup from '@/componentNew/base/Popup';
import MicroPanel from '@/componentNew/base/Panel/Micro';
import { getMicroAppInfo, IMicroAppInfo } from '@/microApp';

// 博客
const Blog = ({ className }: any) => {
  const microAppKey = 'blog';
  const microAppInfo: IMicroAppInfo | null = getMicroAppInfo(microAppKey);

  return (
    <Popup
      width={1024}
      panel={() => <MicroPanel microAppInfo={microAppInfo} />}
      link={microAppInfo.entry}
      needTrack
    >
      <div className={className}><App /></div>
    </Popup>
  );
};

export default Blog;
