import React, { useState } from 'react';
import App from './app';
import Popup from '@/componentNew/base/Popup';
import MicroPanel from '@/componentNew/base/Panel/Micro';
import IframePanel from '@/componentNew/base/Panel/Iframe';
import { getMicroAppInfo, IMicroAppInfo } from '@/microApp';

export const data = {
  main: { type: 'micro', name: '文件上传', key: 'app_files' },
  sub: [
    { type: 'iframe', name: '图片压缩', url: 'https://tinypng.com/' },
    { type: 'iframe', name: '智能配音', url: 'https://zenvideo.qq.com/smart/tts' },
    { type: 'iframe', name: '图片编辑', url: 'https://tool.browser.qq.com/img_edit_canvas.html' },
    { type: 'iframe', name: '视频编辑', url: 'https://www.capcut.cn/web' },
  ]
};

interface TPopupInfo {
  type: 'iframe' | 'micro';
  info: IMicroAppInfo | { name: string; url: string; }
};

// 图床
const Files = ({ className }: any) => {
  const [popupInfo, setPopupInfo] = useState<TPopupInfo | null>(null);
  const onClickItem = (item: any) => {
    setPopupInfo({
      type: item.type,
      info: item.type === 'iframe' ? item : getMicroAppInfo(item.key)
    });
  };
  return (
    <Popup
      width={1024}
      panel={() => popupInfo && (
        popupInfo.type === 'iframe' ? <IframePanel item={popupInfo.info as { name: string; url: string; }} /> : <MicroPanel microAppInfo={popupInfo.info} />
      )}
      link={(popupInfo as any)?.info.url || (popupInfo as any)?.info.entry}
      needTrack
    >
      <div className={className}><App data={data} onClickItem={(item: any) => onClickItem(item)} /></div>
    </Popup>
  );
};

export default Files;
