import { StateCreator } from 'zustand';
// import setCssVariables from '@/utils/setCssVariables';
import isDarkMode from '@/utils/isDarkMode';

export interface SystemSlice {
  theme: 'dark' | 'light';
  fullscreen: boolean;
  lifestyle: 'standard' | 'minimalist';
  toggleTheme: (theme: 'dark' | 'light') => void;
  toggleFullScreen: (v: boolean) => void;
  toggleLifestyle: (lifestyle: 'standard' | 'minimalist') => void;
};

export const createSystemSlice: StateCreator<SystemSlice> = (set) => ({
  theme: isDarkMode() ? 'dark' : 'light',
  fullscreen: false,
  lifestyle: 'standard',
  toggleTheme: (theme) => {
    // setCssVariables(theme === 'dark' ? themeDark : themeLight);
    document.documentElement.setAttribute('data-theme', theme);
    set(() => ({ theme }));
  },
  toggleFullScreen: (v) => {
    set(() => {
      return { fullscreen: v };
    });
  },
  toggleLifestyle: (lifestyle) => {
    set(() => ({ lifestyle }));
  },
});
