import React, { memo, useEffect } from 'react';
import ConfigProvider from '@/componentNew/base/ConfigProvider';
import Desktop from '@/componentNew/system/Desktop';
import Taskbar from '@/componentNew/system/Taskbar';
import Apps from '@/componentNew/system/Apps';
import useTrackInIt from '@/hooks/useTrack';
import { useSystemStore } from '@/stores';

const Index = (): React.ReactElement => {
  useTrackInIt();

  // 初始化主题色
  const theme = useSystemStore((state) => state.theme);
  const toggleTheme = useSystemStore((state) => state.toggleTheme);
  useEffect(() => toggleTheme(theme), []);

  return (
    <ConfigProvider prefixCls="eleven-os">
      <Desktop>
        <Taskbar />
        <Apps />
      </Desktop>
    </ConfigProvider>
  );
};

export default memo(Index);
