import React, { useState, useRef, useEffect } from 'react';
import { Carousel } from 'antd';
import Icon from '@/componentNew/base/Icon';
import DefaultCard from '@/componentNew/base/Card/DefaultCard';
import styles from './app.less';
import Popup from '@/componentNew/base/Popup';
import MicroPanel from '@/componentNew/base/Panel/Micro';
import { getMicroAppInfo, IMicroAppInfo } from '@/microApp';

// 游戏中心
const Games = ({
  data = [],
}: any) => {
  const [slideIndex, setSlideIndex] = useState(0);
  const [microAppInfo, setMicroAppInfo] = useState<IMicroAppInfo | null>(null);
  const coverCarouselRef: any = useRef(null);
  const infoCarouselRef: any = useRef(null);
  const coverSettings = {
    dots: false,
    centerMode: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    centerPadding: '60px',
    focusOnSelect: true,
    beforeChange: (current: any, next: any) => setSlideIndex(next)
  };
  useEffect(() => {
    coverCarouselRef.current.goTo(slideIndex);
    infoCarouselRef.current.goTo(slideIndex);
  }, [slideIndex]);

  const handleClick = (index: any) => {
    console.log(data[index].key);
    setMicroAppInfo(getMicroAppInfo(data[index]?.key) || null);
  };

  return (
    <DefaultCard className={styles.games}>
      <div className={styles.cover}>
        <Carousel
          ref={coverCarouselRef}
          {...coverSettings}
        >
          {data.map((item: any, index: any) => (
            <div key={index}>
              <div className={styles.item}>
                <div className={styles.icon} style={{ '--item-color': item.color } as any}>
                  <Icon name={item.icon} />
                </div>
              </div>
            </div>
          ))}
        </Carousel>
      </div>
      <Popup
        width={1024}
        panel={() => microAppInfo && <MicroPanel microAppInfo={microAppInfo} />}
        link={(microAppInfo as any)?.entry}
        needTrack
      >
        <div className={styles.info}>
          <Carousel
            effect="fade"
            dots={false}
            ref={infoCarouselRef}
          >
            {data.map((item: any, index: any) => (
              <div key={index}>
                <div className={styles.item} onClick={() => handleClick(slideIndex)}>
                  <div className={styles.title}>{item.name}</div>
                  <div className={styles.info}>{item.info}</div>
                </div>
              </div>
            ))}
          </Carousel>
        </div>
      </Popup>
    </DefaultCard>
  );
};

export default Games;
