export type IMicroAppKey = keyof typeof microAppMap;

export const getMicroAppInfo = (key: IMicroAppKey) => {
  const microAppInfo = microAppMap[key];
  let entry = microAppInfo?.githubEntry;
  if (IS_GITHUB) {
    entry = microAppInfo?.githubEntry || entry;
  }
  if (IS_GITEE) {
    entry = microAppInfo?.giteeEntry || entry;
  }
  if (IS_DEV) {
    entry = microAppInfo?.devEntry || entry;
  }
  return {
    name: microAppInfo?.name,
    info: microAppInfo?.info,
    entry,
  };
};
