import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import { TipsMessageMap } from './constants';
import { sleep } from '@/utils/tool';
import DefaultCard from '@/componentNew/base/Card/DefaultCard';
import styles from './app.less';

const App: React.FC = () => {
  const tipsMessageList: string[] = [...TipsMessageMap.management.list, ...TipsMessageMap.life.list];
  const [tip, setTip] = useState(`“${tipsMessageList[Math.random() * tipsMessageList.length >> 0]}”`);
  const [switchTip, setSwitchTip] = useState(false);

  const sayTipsMessage = async () => {
    setSwitchTip(true);
    await sleep(1500);
    setTip(`“${tipsMessageList[Math.random() * tipsMessageList.length >> 0]}”`);
    setSwitchTip(false);
  };

  useEffect(() => {
    const timeId = setTimeout(sayTipsMessage, 10000);
    return () => clearTimeout(timeId);
  }, [tip]);

  return (
    <DefaultCard>
      <div className={classnames(styles.tips, switchTip && styles.switchTip)}>
        {tip}
      </div>
    </DefaultCard>
  );
};

export default App;
